<!--  -->
<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item three">
        <span class="title">ID</span>
        <el-input v-model="requestData.nnAwardId" clearable></el-input>
      </div>
      <div class="search-item ten">
        <span class="title"></span>
        <el-input v-model="requestData.playerName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="nnBetInfoId" label="ID" width="100"></el-table-column>
        <el-table-column prop="awardId" label="ID" width="100"></el-table-column>
        <el-table-column prop="userName" label="" min-width="180">
          <template slot-scope="scope">
            <div style="float:left;">
              <img style="display: inline-block;width: 30px;vertical-align: middle;" :src="scope.row.userLogo" alt="">
              {{scope.row.userName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="betType" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.betType===1" style="color: #666"></span>
            <span v-if="scope.row.betType===2" style="color: #eb453c"></span>
          </template>
        </el-table-column>
        <el-table-column prop="betPrice" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: #666"></span>
            <span v-if="scope.row.status===2" style="color: #eb453c"></span>
            <span v-if="scope.row.status===3" style="color: #74ebab"></span>
            <span v-if="scope.row.status===4" style="color: #0eebe3"></span>
          </template>
        </el-table-column>
        <el-table-column prop="damagePrice" label="" width="100"></el-table-column>
        <el-table-column prop="collarPrice" label=""></el-table-column>
        <el-table-column prop="nnType" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.nnType===10" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===1" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===2" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===3" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===4" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===5" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===6" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===7" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===8" style="color: #74EBAB"></span>
            <span v-if="scope.row.nnType===9" style="color: #74EBAB"></span>
          </template>
        </el-table-column>
        <el-table-column prop="betOrderBy" label=""></el-table-column>
        <el-table-column prop="betDate" label=""></el-table-column>
        <el-table-column prop="collarType" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.collarType===1" style="color: rgba(48,235,209,0.42)"></span>
            <span v-if="scope.row.collarType===2" style="color: #e7eb18"></span>
          </template>
        </el-table-column>
        <el-table-column prop="collarDate" label=""></el-table-column>
        <el-table-column prop="damageDate" label=""></el-table-column>
        <el-table-column prop="loseWinNum" label=""></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>ID</label>{{dialogData.nnBetInfoId}}</p>
        <p><label>ID</label>{{dialogData.awardId}}</p>
        <p><label></label>
          <img style="display: inline-block;width: 30px;vertical-align: middle;" :src="dialogData.userLogo" alt="">
          {{dialogData.userName}}
        </p>
        <p><label></label>{{dialogData.betOrderBy}}</p>
        <p><label></label>
          <span v-if="dialogData.betType === 1"></span>
          <span v-if="dialogData.betType === 2"></span>
        </p>
        <p><label></label>{{dialogData.betPrice}}</p>
        <p><label></label>
          <span v-if="dialogData.status === 1"></span>
          <span v-if="dialogData.status === 2"></span>
          <span v-if="dialogData.status === 3"></span>
          <span v-if="dialogData.status === 4"></span>
        </p>
        <p><label></label>{{dialogData.damagePrice}}</p>
        <p><label></label>{{dialogData.collarPrice}}</p>
        <p><label></label>
          <span v-if="dialogData.nnType === 10"></span>
          <span v-if="dialogData.nnType === 1"></span>
          <span v-if="dialogData.nnType === 2"></span>
          <span v-if="dialogData.nnType === 3"></span>
          <span v-if="dialogData.nnType === 4"></span>
          <span v-if="dialogData.nnType === 5"></span>
          <span v-if="dialogData.nnType === 6"></span>
          <span v-if="dialogData.nnType === 7"></span>
          <span v-if="dialogData.nnType === 8"></span>
          <span v-if="dialogData.nnType === 9"></span>
        </p>
        <p><label></label>{{dialogData.betDate}}</p>
        <p><label></label>
          <span v-if="dialogData.collarType === 1"></span>
          <span v-if="dialogData.collarType === 2"></span>
        </p>
        <p><label></label>{{dialogData.collarDate}}</p>
        <p><label></label>{{dialogData.damageDate}}</p>
        <p><label></label>{{dialogData.loseWinNum}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameNnBetInfoNnBetInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, nnAwardId: '', playerName: ''},
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._gameNnAwardNnAwardList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._gameNnAwardNnAwardList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameNnAwardNnAwardList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._gameNnAwardNnAwardList()
    },
    async _gameNnAwardNnAwardList () {
      const res = await gameNnBetInfoNnBetInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameNnAwardNnAwardList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



