<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item three">
        <span class="title"></span>
        <el-input v-model="requestData.phone" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="userDataList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="id" label="Id" width="80" fixed></el-table-column>
        <el-table-column prop="bankName" label="" min-width="180" fixed>
          <template slot-scope="scope">
            <div style="float:left;">
              <img style="display: inline-block;width: 30px;vertical-align: middle;" :src="scope.row.userLogo" alt="">
              {{scope.row.userName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="attestationType" label="" width="80">
          <template slot-scope="scope">
            <span v-if="scope.row.sex === 1"></span>
            <span v-if="scope.row.sex === 2"></span>
            <span v-if="scope.row.sex === 3"></span>
          </template>
        </el-table-column>
        <el-table-column prop="phone" label="" width="100"></el-table-column>
        <el-table-column prop="countryName" label="" width="150">
          <template slot-scope="scope">
            {{scope.row.countryName + scope.row.provinceName + scope.row.cityName}}
          </template>
        </el-table-column>
        <el-table-column prop="trueName" label="" width="90"></el-table-column>
        <el-table-column prop="wechatName" label="" width="100"></el-table-column>
        <el-table-column prop="qqName" label="qq" width="100"></el-table-column>
        <el-table-column prop="parentName" label="" width="90"></el-table-column>
        <el-table-column prop="qqName" label="" width="80">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green"></span>
            <span v-if="scope.row.status===2" style="color: #eb453c"></span>
            <span v-if="scope.row.status===3" style="color: #666"></span>
          </template>
        </el-table-column>
        <el-table-column prop="accountPrice" label="" width="120"></el-table-column>
        <el-table-column prop="freezePrice" label="" width="120"></el-table-column>
        <el-table-column prop="totalRechargePrice" label="" width="120"></el-table-column>
        <el-table-column prop="totalWithdrawPrice" label="" width="120"></el-table-column>
        <el-table-column prop="totalTransferPrice" label="" width="120"></el-table-column>
        <el-table-column prop="registerDate" label="" width="150"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="330">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
            <el-button type="primary" @click.native.prevent="_friendList(scope.row)" size="mini"></el-button>
            <el-button type="primary" @click.native.prevent="toRecharge(scope.row)" size="mini"></el-button>
            <el-button type="primary" @click.native.prevent="toWithdraw(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>Id</label>{{dialogData.id}}</p>
        <p><label></label>{{dialogData.phone}}</p>
        <p><label></label><span v-if="dialogData.status===1" style="color: green"></span><span v-if="dialogData.status===2" style="color: #eb453c"></span><span v-if="dialogData.status===3" style="color: #666"></span></p>
        <p><label></label>{{dialogData.userName}}</p>
        <p><label></label><img :src="dialogData.userLogo" @click="showImg(dialogData.userLogo)" alt=""></p>
        <p><label></label><span v-if="dialogData.sex === 1"></span><span v-if="dialogData.sex === 2"></span><span v-if="dialogData.sex === 3"></span></p>
        <p><label></label>{{dialogData.trueName}}</p>
        <p><label></label>{{dialogData.countryName+dialogData.provinceName+dialogData.cityName}}</p>
        <p><label></label>{{dialogData.wechatName}}</p>
        <p><label>qq</label>{{dialogData.qqName}}</p>
        <p><label></label>{{dialogData.parentName}}</p>
        <p><label></label>{{dialogData.accountPrice}}</p>
        <p><label></label>{{dialogData.freezePrice}}</p>
        <p><label></label>{{dialogData.totalRechargePrice}}</p>
        <p><label></label>{{dialogData.totalWithdrawPrice}}</p>
        <p><label></label>{{dialogData.totalTransferPrice}}</p>
        <p><label></label>{{dialogData.registerDate}}</p>
      </div>
    </el-dialog>
    <el-dialog :close-on-click-modal="false" title="" :visible.sync="dialogFormVisible2" center width="500px">
      <div class="search-zu">
        <div class="search-item four">
          <span class="title"></span>
          <el-input v-model="dialogSearch.friendsName" clearable></el-input>
        </div>
        <el-button size="mini" type="primary" icon="el-icon-search" @click="search2"></el-button>
      </div>
      <div class="my-table cl center">
        <el-table :data="friendsList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
          <el-table-column prop="friendsName" label="" width="100"></el-table-column>
          <el-table-column prop="remark" label="" width="100"></el-table-column>
          <el-table-column prop="status" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.status===1" style="color: green"></span>
              <span v-if="scope.row.status===2" style="color: #eb453c"></span>
              <span v-if="scope.row.status===3" style="color: #666"></span>
            </template>
          </el-table-column>
          <el-table-column prop="createDate" label="" width="150"></el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange1"
          @current-change="handleCurrentChange1"
          :current-page="dialogSearch.pageNum"
          :page-sizes="[20, 30, 40, 50]"
          :page-size="dialogSearch.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count">
        </el-pagination>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible3" width="350px">
      <el-form :model="userRecharge" :rules="rules" label-width="100px" size="mini">

        <el-form-item label="" prop="rechargeNum">
          <el-input style="width:193px" v-model="userRecharge.rechargeNum" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input style="width:193px" v-model="userRecharge.remark" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2"> </el-button>
        <el-button type="primary" @click="submitRecharge"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible4" width="350px">
      <el-form :model="userWithdraw" :rules="rules" label-width="100px" size="mini">

        <el-form-item label="" prop="withdrawNum">
          <el-input style="width:193px" v-model="userWithdraw.withdrawNum" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input style="width:193px" v-model="userWithdraw.remark" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3"> </el-button>
        <el-button type="primary" @click="submitWithdraw"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import {userList, userUpdateUserStatus, userUserFriendsList, changeAccountRecharge, changeAccountWithdraw} from '@/api'
import {checkInt, checkDefeatInt} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', phone: ''},
      userDataList: [],
      choseArr: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogFormVisible3: false,
      dialogFormVisible4: false,
      userRecharge: {userId: '', remark: '', rechargeNum: ''},
      userWithdraw: {userId: '', remark: '', withdrawNum: ''},
      dialogData: {},
      userIds: [],
      dialogSearch: {friendsName: '', pageNum: 1, pageSize: 20, userId: ''},
      dialogFormVisible2: false,
      friendsList: [],
      count: 0,
      rules: {
        withdrawNum: [
          { validator: checkDefeatInt, trigger: 'change' }
        ],
        rechargeNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._userList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._userList()
    },
    handleSizeChange1 (val) { // 
      this.dialogSearch.pageSize = val
      this._friendList()
    },
    handleCurrentChange1 (val) { // page
      this.dialogSearch.pageNum = val
      this._friendList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userList()
    },
    search2 () {
      this.dialogSearch.pageNum = 1
      this._friendList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', phone: ''}
      this._userList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
      this.choseArr = val
    },
    async _userList () {
      const res = await userList(this.requestData)
      if (res.result === '0') {
        this.userDataList = res.datas.userList
        this.totalNum = res.datas.totalRow
      }
    },
    freeze (row) {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: ''})
      } else {
        this.userIds = this.choseArr.map((item) => {
          return item.id
        })
        let tip
        row === 1 ? tip = '' : row === 2 ? tip = '' : tip = ''
        this.$confirm(tip, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    async _freeze (row) {
      const res = await userUpdateUserStatus({status: row, ids: this.userIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this._userList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    },
    async _friendList (row) {
      this.dialogFormVisible2 = true
      this.dialogSearch.userId = row.id
      const res = await userUserFriendsList(this.dialogSearch)
      if (res.result === '0') {
        this.friendsList = res.datas.list
        this.count = res.datas.totalRow
      }
    },
    toRecharge (row) {
      this.userRecharge.userId = row.id
      this.dialogFormVisible3 = true
    },
    toWithdraw (row) {
      this.userWithdraw.userId = row.id
      this.dialogFormVisible4 = true
    },
    // 
    cancle2 () {
      this.dialogFormVisible3 = false
      this.setReset2()
    },
    setReset2 () {
      this.userRecharge = {userId: '', remark: '', rechargeNum: ''}
    },
    // 
    cancle3 () {
      this.dialogFormVisible4 = false
      this.setReset3()
    },
    setReset3 () {
      this.userWithdraw = {userId: '', remark: '', withdrawNum: ''}
    },
    // 
    async submitRecharge () {
      if (this.userRecharge.rechargeNum === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.userRecharge.rechargeNum !== '' && !/^[1-9]*[1-9][0-9]*$/.test(this.userRecharge.rechargeNum)) {
        this.$message({type: 'warning', message: ''})
      } else {
        const res = await changeAccountRecharge(this.userRecharge)
        if (res.result === '1') {
          this._userList()
          this.dialogFormVisible3 = false
          this.setReset2()
        }
      }
    },
    // 
    async submitWithdraw () {
      if (this.userWithdraw.withdrawNum === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.userWithdraw.withdrawNum !== '' && !/^-[1-9]*[1-9][0-9]*$/.test(this.userWithdraw.withdrawNum)) {
        this.$message({type: 'warning', message: ''})
      } else {
        const res = await changeAccountWithdraw(this.userWithdraw)
        if (res.result === '1') {
          this._userList()
          this.dialogFormVisible4 = false
          this.setReset3()
        }
      }
    }
  },
  created () {
    this._userList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
.el-dialog .cl{
  height: 350px;
}
</style>



