<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="requestData.goodsName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title"></span>
        <el-select clearable v-model="requestData.status" placeholder="">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="goodsConvertList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="goodsConvertId" label="Id" width="80"></el-table-column>
        <el-table-column prop="goodsName" label="" width="100"></el-table-column>
        <el-table-column prop="priceNum" label="" width="100"></el-table-column>
        <el-table-column prop="convertName" label="" width="100"></el-table-column>
        <el-table-column prop="convertPhone" label="" width="100"></el-table-column>
        <el-table-column prop="convertAddress" label="" width="100"></el-table-column>
        <el-table-column prop="userName" label="" width="100"></el-table-column>
        <el-table-column prop="createDate" label="" width="100"></el-table-column>
        <el-table-column prop="deliverSysUserName" label="" width="100"></el-table-column>
        <el-table-column prop="deliverDate" label="" width="100"></el-table-column>
        <el-table-column prop="finishSysUserName" label="" width="100"></el-table-column>
        <el-table-column prop="finishDate" label="" width="100"></el-table-column>
        <el-table-column prop="status" label="" width="80">
        <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: #eb453c"></span>
            <span v-if="scope.row.status===2" style="color: green"></span>
            <span v-if="scope.row.status===3" style="color: green"></span>
        </template>
        </el-table-column>
        <el-table-column prop="createDate" label="" width="150"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="">
         <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p>
          <el-button type="primary" size="mini" @click="editStatus(1)" :disabled="dialogData.status != '1'"></el-button>
          <el-button type="primary" size="mini" @click="editStatus(2)" :disabled="dialogData.status != '2'"></el-button>
        </p>
        <p><label>Id</label>{{dialogData.goodsConvertId}}</p>
        <p><label></label>{{dialogData.goodsName}}</p>
        <p>
          <label></label>
          <span v-if="dialogData.status===1" style="color: #eb453c"></span>
          <span v-if="dialogData.status===2" style="color: green"></span>
          <span v-if="dialogData.status===3" style="color: green"></span>
        </p>
        <p><label></label>{{dialogData.priceNum}}</p>
        <p><label></label>{{dialogData.convertName}}</p>
        <p><label></label>{{dialogData.convertPhone}}</p>
        <p><label></label>{{dialogData.convertAddress}}</p>
        <p><label></label>{{dialogData.userName}}</p>
        <p><label></label>{{dialogData.createDate}}</p>
        <p><label></label>{{dialogData.deliverSysUserName}}</p>
        <p><label></label>{{dialogData.deliverDate}}</p>
        <p><label></label>{{dialogData.finishSysUserName}}</p>
        <p><label></label>{{dialogData.finishDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {goodsConvertList, goodsConvertEditStatus} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, goodsName: '', status: ''},
      goodsConvertList: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      count: 0,
      options: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}]
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._convertList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._convertList()
    },
    cancle () {
      this.dialogFormVisible = false
    },
    search () {
      this.requestData.pageNum = 1
      this._convertList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, goodsName: '', status: ''}
      this._convertList()
    },
    async _convertList () {
      const res = await goodsConvertList(this.requestData)
      if (res.result === '0') {
        this.goodsConvertList = res.datas.goodsConvertList
        this.totalNum = res.datas.totalRow
      }
    },
    async editStatus (type) {
      const res = await goodsConvertEditStatus({goodsConvertId: this.dialogData.goodsConvertId, type: type})
      if (res.result === '1') {
        this.dialogFormVisible = false
        this._convertList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this._convertList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



