<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addConfig"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="id" label="Id" width="80"></el-table-column>
        <el-table-column prop="collarPrice" label="" width="100"></el-table-column>
        <el-table-column prop="collarReward" label="" width="100"></el-table-column>
        <el-table-column prop="status" label="" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green"></span>
            <span v-if="scope.row.status===2" style="color: #eb453c"></span>
            <span v-if="scope.row.status===3" style="color: #666"></span>
          </template>
        </el-table-column>
        <el-table-column prop="createDate" label=""></el-table-column>
        <el-table-column prop="modifySysUserName" label="" width="90"></el-table-column>
        <el-table-column prop="modifyDate" label=""></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="270">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
            <el-button v-if="scope.row.status===1" type="primary" @click.native.prevent="freeze(scope.row, 1)" size="mini"></el-button>
            <el-button v-else type="primary" @click.native.prevent="freeze(scope.row, 2)" size="mini"></el-button>
            <el-button type="primary" @click.native.prevent="freeze(scope.row, 3)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '': ''" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" :rules="rules" label-width="80px" size="mini">
        <el-form-item label="" prop="collarPrice">
          <el-input style="width:180px" v-model="form.collarPrice" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="collarReward">
          <el-input style="width:180px" v-model="form.collarReward" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameJlCollarConfigList, gameJlCollarConfigUpdateStatus, gameJlCollarConfigEdit, gameJlCollarConfigAdd} from '@/api'
import {checkPic} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20},
      ConfigList: [],
      dialogFormVisible: false,
      form: {collarPrice: '', collarReward: ''},
      count: 0,
      isedit: false,
      rules: {
        collarPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        collarReward: [
          { validator: checkPic, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.requestData.pageNum = val
      this._gameJlCollarConfigList()
    },
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._gameJlCollarConfigList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20}
      this._gameJlCollarConfigList()
    },
    async _gameJlCollarConfigList () {
      const res = await gameJlCollarConfigList(this.requestData)
      if (res.result === '0') {
        this.ConfigList = res.datas.collarConfigList
        this.count = res.datas.totalRow
      }
    },
    freeze (row, type) {
      let tip
      type === 1 ? tip = '' : type === 2 ? tip = '' : tip = ''
      this.$confirm(tip, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._freeze(row, type)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    async _freeze (row, type) {
      let status = type === 1 ? 2 : type === 2 ? 1 : 3
      const res = await gameJlCollarConfigUpdateStatus({status: status, id: row.id})
      if (res.result === '1') {
        this._gameJlCollarConfigList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.form.id = row.id
      this.form.collarPrice = row.collarPrice
      this.form.collarReward = row.collarReward
      this.isedit = true
    },
    addConfig () {
      this.dialogFormVisible = true
      this.form = {collarPrice: '', collarReward: ''}
      this.isedit = false
    },
    async submit () {
      if (this.form.collarPrice === '') {
        this.$message({type: 'warning', message: ''})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.collarPrice)) {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.collarReward === '') {
        this.$message({type: 'warning', message: ''})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.collarReward)) {
        this.$message({type: 'warning', message: ''})
      } else {
        let res
        this.isedit ? res = await gameJlCollarConfigEdit(this.form) : res = await gameJlCollarConfigAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this._gameJlCollarConfigList()
        }
      }
    }
  },
  created () {
    this._gameJlCollarConfigList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



