export function isPC () {
  let userAgentInfo = navigator.userAgent
  let Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod']
  let flag = true
  for (var v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      flag = false
      break
    }
  }
  return flag
}

export function checkPhone (rule, value, callback) { // Phone
  if (!/^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])(\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])){3}$/.test(value)) callback(new Error(''))
}

export function checkMobile (rule, value, callback) { // Mobile
  if (!/^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])(\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])){3}$/.test(value)) callback(new Error(''))
}

export function checkQQ (rule, value, callback) { // QQ
  if (!/^((\d{1,3}(,\d{3})*)|(\d+))(\.\d{2})?$/.test(value)) callback(new Error('QQ'))
}

export function checkIP (rule, value, callback) { // IP
  if (!/^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])(\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])){3}$/.test(value)) callback(new Error('IP'))
}

export function checkEMAIL (rule, value, callback) { // EMAIL
  if (!/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value)) callback(new Error('EMAIL'))
}

export function checkPic (rule, value, callback) { // 
  if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(value)) callback(new Error(''))
}

export function checkNumber (rule, value, callback) { // 
  if (!/^-?\d+$/.test(value)) callback(new Error(''))
}

export function checkInt (rule, value, callback) { // >0
  if (!/^[1-9]*[1-9][0-9]*$/.test(value)) callback(new Error(''))
}

export function checkInt2 (rule, value, callback) { // >=0
  if (!/^[1-9]*[0-9][0-9]*$/.test(value)) callback(new Error(''))
}

export function checkDefeatInt (rule, value, callback) { // <0
  if (!/^-[1-9]*[1-9][0-9]*$/.test(value)) callback(new Error(''))
}

export function checkDefeatInt2 (rule, value, callback) { // <=0
  if (!/^-[1-9]*[0-9][0-9]*$/.test(value)) callback(new Error(''))
}

export function checkSmallNumber (rule, value, callback) { // 
  if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(value)) callback(new Error(''))
}

export function checkOneSmallNumber (rule, value, callback) { // 1
  if (!/^(([^0][0-9]+|0)\.([0-9]{1}))$/.test(value)) callback(new Error('1'))
}

export function checkTwoSmallNumber (rule, value, callback) { // 2
  if (!/^(([0-9]+|0)\.([0-9]{2}))$/.test(value)) callback(new Error('2'))
}

export function checkFourSmallNumber (rule, value, callback) { // 4
  if (!/^(([^0][0-9]+|0)\.([0-9]{4}))$/.test(value)) callback(new Error('4'))
}

export function checkOneFlaseSmallNumber (rule, value, callback) { // 1
  if (!/^(([0-9]+|0)\.([0-9]{1}))$/.test(value)) callback(new Error('1'))
}

export function checkTwoFlaseSmallNumber (rule, value, callback) { // 2
  if (!/^(([0-9]+|0)\.([0-9]{2}))$/.test(value)) callback(new Error('22.00'))
}

export function checkFourFlaseSmallNumber (rule, value, callback) { // 4
  if (!/^(([0-9]+|0)\.([0-9]{4}))$/.test(value)) callback(new Error('42.0000'))
}
