<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="serachData.goodsName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addGood"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="goodsList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="goodsLogo" label="LOGO" width="110">
          <template slot-scope="scope">
            <img style="width: 90px" :src="baseUrl+scope.row.goodsLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="goodsName" label="" width="100"></el-table-column>
        <el-table-column prop="goodsInfo" label="" min-width="120"></el-table-column>
        <el-table-column prop="goodsNum" label="" width="100"></el-table-column>
        <el-table-column prop="goodsPayNum" label="" width="100"></el-table-column>
        <el-table-column prop="goodsSaleNum" label="" width="110"></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a"></span>
            <span v-if="scope.row.status === 2" style="color:#eb453c"></span>
            <span v-if="scope.row.status === 3" style="color:#999"></span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label=""></el-table-column>
        <el-table-column prop="createDate" label=""></el-table-column>
        <el-table-column prop="modifySysUserName" label=""></el-table-column>
        <el-table-column prop="modifyDate" label=""></el-table-column>
        <el-table-column label="" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click.native.prevent="detailsFn(scope.row)" ></el-button>
        </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.page"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '': ''" :visible.sync="dialogFormVisible" width="450px">
      <el-form :model="form" :rules="rules" label-width="90px" size="mini">
        <el-form-item  label="LOGO">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="">
          <el-input style="width:280px" v-model="form.goodsName" autocomplete="off"></el-input>
        </el-form-item>
         <el-form-item label="">
          <el-input type="textarea" :rows="3" style="width:280px" v-model="form.goodsInfo" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goodsNum">
          <el-input style="width:280px" v-model="form.goodsNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goodsPayNum">
          <el-input style="width:280px" v-model="form.goodsPayNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.status" active-color="#13ce66" active-value="1" inactive-value="2" inactive-color="#ff4949" active-text="" inactive-text=""></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
     <el-dialog title="" :visible.sync="dialogFormVisible2" center width="500px">
      <el-form class="form" ref="form" label-width="110px" label-position="left" size="mini">
        <el-form-item label="ID">
          <div>{{dialogData.id}}</div>
        </el-form-item>
        <el-form-item label="Logo" prop="goodsLogo" width="100">
          <div> <img style="width: 90px" :src="baseUrl + dialogData.goodsLogo" @click="showImg(baseUrl + dialogData.goodsLogo)" alt=""></div>
        </el-form-item>
        <el-form-item label="">
          <div>{{dialogData.goodsName}}</div>
        </el-form-item>
        <el-form-item label="">
          <div>{{dialogData.goodsInfo}}</div>
        </el-form-item>
        <el-form-item label="">
          <div>{{dialogData.goodsNum}}</div>
        </el-form-item>
        <el-form-item label="">
          <div>{{dialogData.goodsPayNum}}</div>
        </el-form-item>
        <el-form-item label="">
          <div>{{dialogData.goodsSaleNum}}</div>
        </el-form-item>
        <el-form-item label="">
          <div>
            <span v-if="dialogData.status === 1" style="color:#67c23a"></span>
            <span v-if="dialogData.status === 2" style="color:#eb453c"></span>
            <span v-if="dialogData.status === 3" style="color:#999"></span>
          </div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import {goodsList, goodsAdd, goodsEdit, goodsUpdateStatus, goodsInfo} from '@/api'
import {baseUrl, checkInt} from '@/utils/commom'
import MyUpload from '@/components/Upload'
export default {
  data () {
    return {
      dialogFormVisible: false,
      dialogFormVisible2: false,
      isedit: false,
      form: {goodsName: '', goodsLogo: '', goodsInfo: '', goodsNum: '', goodsPayNum: '', status: '1'},
      choseArr: [],
      id: [],
      dialogData: {},
      count: 1,
      serachData: {pageNum: 1, pageSize: 20, goodsName: ''},
      goodsList: [],
      baseUrl: '',
      option: {
        type: 1,
        file: [],
        count: 1
      },
      rules: {
        goodsNum: [
          { validator: checkInt, trigger: 'change' }
        ],
        goodsPayNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      }
    }
  },
  components: {
    MyUpload
  },
  methods: {
    handleCurrentChange (val) {
      this.serachData.pageNum = val
      this.getGoodsList()
    },
    handleSizeChange (val) { // 
      this.serachData.pageSize = val
      this.getGoodsList()
    },
    async detailsFn (row) {
      const res = await goodsInfo({id: row.id})
      if (res.result === '0') {
        this.dialogData = res.datas
        this.dialogFormVisible2 = true
      }
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: ''})
      } else {
        this.id = this.choseArr.map((item) => {
          return item.id
        })
        let tip
        row === 1 ? tip = '' : row === 2 ? tip = '' : tip = ''
        this.$confirm(tip, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    async _freeze (row) {
      const res = await goodsUpdateStatus({status: row, id: this.id.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.getGoodsList()
      }
    },
    async getGoodsList () {
      const res = await goodsList(this.serachData)
      if (res.result === '0') {
        this.goodsList = res.datas.goodsList
        this.count = res.datas.totalRow
      }
    },
    async edit (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: ''})
      } else {
        this.dialogFormVisible = true
        this.option.file = []
        this.form.id = this.choseArr[0].id
        this.form.goodsName = this.choseArr[0].goodsName
        this.form.goodsLogo = this.choseArr[0].goodsLogo
        this.form.goodsInfo = this.choseArr[0].goodsInfo
        this.form.goodsNum = this.choseArr[0].goodsNum
        this.form.goodsPayNum = this.choseArr[0].goodsPayNum
        this.form.status = String(this.choseArr[0].status)
        this.option.file.push({file: this.choseArr[0].goodsLogo})
        this.isedit = true
      }
    },
    async submit () {
      if (this.form.goodsLogo === '') {
        this.$message({type: 'warning', message: 'logo'})
      } else if (this.form.goodsName === '' && !this.isedit) {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.goodsInfo === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.goodsNum === '') {
        this.$message({type: 'warning', message: ''})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.form.goodsNum)) {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.goodsPayNum === '') {
        this.$message({type: 'warning', message: ''})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.form.goodsPayNum)) {
        this.$message({type: 'warning', message: ''})
      } else if (!this.isedit) {
        const res = await goodsAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this.serachData.pageNum = 1
          this.getGoodsList()
          this.setReset()
        }
      } else {
        const res = await goodsEdit(this.form)
        if (res.result === '1') {
          this.getGoodsList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addGood () {
      this.setReset()
      this.dialogFormVisible = true
      this.isedit = false
    },
    upfiles (file) {
      this.form.goodsLogo = file.files[0].file
    },
    updelete (file) {
      this.form.goodsLogo = ''
    },
    cancle () {
      this.dialogFormVisible = false
    },
    setReset () {
      this.option.file = []
      this.form = {goodsName: '', goodsLogo: '', goodsInfo: '', goodsNum: '', goodsPayNum: '', status: '1'}
    },
    refresh () {
      this.serachData = {goodsName: '', pageNum: 1, pageSize: 20}
      this.getGoodsList()
    },
    search () {
      this.serachData.pageNum = 1
      this.getGoodsList()
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this.getGoodsList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
  .el-dialog__body .my-table{
    height: 350px;
  }
}
</style>



