<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <el-tabs type="border-card" style="height: calc(100% - 40px);" @tab-click="handleClick" v-model="type">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userName" label=""></el-table-column>
            <el-table-column prop="orderNo" label=""></el-table-column>
            <el-table-column prop="payType" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.payType===1"></span>
                <span v-if="scope.row.payType===2"></span>
              </template>
            </el-table-column>
            <el-table-column prop="rechargePrice" label=""></el-table-column>
            <el-table-column prop="prPrice" label=""></el-table-column>
            <el-table-column prop="status" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1"></span>
                <span v-if="scope.row.status===2"></span>
                <span v-if="scope.row.status===3"></span>
                <span v-if="scope.row.status===3"></span>
              </template>
            </el-table-column>
            <el-table-column prop="createDate" label=""></el-table-column>
            <el-table-column prop="channelType" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.channelType===1"></span>
                <span v-if="scope.row.channelType===2">BUF</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
    </el-tabs>

  </div>
</template>

<script>
import {bufRechargeList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: ''},
      options: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 4, label: ''}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      dialogFormVisible2: false,
      checkRemark: '',
      id: '',
      type: '1'
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._userRechargeList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._userRechargeList()
    },
    handleClick (val) {
      this.type = val.name
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._userRechargeList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userRechargeList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._userRechargeList()
    },
    async _userRechargeList () {
      const res = await bufRechargeList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    }
  },
  created () {
    this._userRechargeList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>
<style>
.el-tabs--border-card>.el-tabs__content{
  height: calc(100% - 50px);
}
.el-tab-pane{
  height: 100%;
}
</style>



