<!--  -->
<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item three">
        <span class="title"></span>
        <el-input v-model="requestData.invitationUserName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="requestData.status" placeholder="">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="pass"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column prop="id" label="Id" width="80"></el-table-column>
        <el-table-column prop="userName" label=""></el-table-column>
        <el-table-column prop="roomName" label=""></el-table-column>
        <el-table-column prop="invitationUserName" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: #666"></span>
            <span v-if="scope.row.status===5" style="color: #eb453c"></span>
            <span v-if="scope.row.status===2" style="color: green"></span>
            <span v-if="scope.row.status===3" style="color: #eb453c"></span>
            <span v-if="scope.row.status===4" style="color: #eb453c"></span>
            <span v-if="scope.row.status===6" style="color: #666"></span>
          </template>
        </el-table-column>
        <el-table-column prop="checkDate" label=""></el-table-column>
        <el-table-column prop="createDate" label=""></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="100">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status===1" type="primary" @click.native.prevent="nopass(scope.row)" size="mini"></el-button>
            <el-button v-if="scope.row.status===2" type="primary" @click.native.prevent="freeze(scope.row, 5)" size="mini"></el-button>
            <el-button v-if="scope.row.status===5" type="primary" @click.native.prevent="freeze(scope.row, 2)" size="mini"></el-button>
            <el-button v-if="scope.row.status===6" type="primary" @click.native.prevent="freeze(scope.row, 7)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible2" center width="300px">
      <div class="plan-con">
        <div>
          <el-input type="textarea" placeholder="" size="mini" v-model="checkRemark"></el-input>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false;"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameNnUserRoomList, gameNnUserRoomUpdateStatus, gameNnUserRoomCheck, gameNnUserRoomReject} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, roomName: '', status: '', invitationUserName: '', userName: '', slRoomId: ''},
      ConfigList: [],
      count: 0,
      options: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 6, label: ''}],
      choseArr: [],
      userIds: [],
      haspass: false,
      dialogFormVisible2: false,
      checkRemark: ''
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.requestData.pageNum = val
      this._gameNnUserRoomList()
    },
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._gameNnUserRoomList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameNnUserRoomList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, roomName: '', status: '', invitationUserName: '', userName: '', slRoomId: ''}
      this._gameNnUserRoomList()
    },
    async _gameNnUserRoomList () {
      const res = await gameNnUserRoomList(this.requestData)
      if (res.result === '0') {
        this.ConfigList = res.datas.list
        this.count = res.datas.totalRow
      }
    },
    freeze (row, type) {
      let tip
      type === 5 ? tip = '?' : type === 2 ? tip = '?' : tip = '?'
      this.$confirm(tip, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._freeze(row, type)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    async _freeze (row, type) {
      const res = await gameNnUserRoomUpdateStatus({status: type, nnRoomId: row.id})
      if (res.result === '1') {
        this._gameNnUserRoomList()
      }
    },
    pass () {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: ''})
      } else {
        this.userIds = this.choseArr.map((item) => {
          if (item.status > 1) {
            this.haspass = true
          }
          return item.id
        })
        if (this.haspass) {
          this.$message({type: 'warning', message: ''})
        } else {
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.surepass()
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            })
          })
        }
      }
    },
    async surepass () {
      const res = await gameNnUserRoomCheck({userRoomId: this.userIds.join(',')})
      if (res.result === '1') this._gameNnUserRoomList()
    },
    nopass (row) {
      this.dialogFormVisible2 = true
      this.id = row.id
    },
    submit () {
      if (this.checkRemark === '') {
        this.$message({type: 'warning', message: ''})
      } else {
        let tip
        tip = ''
        this.$confirm(tip, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const res = await gameNnUserRoomReject({nnRoomId: this.id, remark: this.checkRemark})
          if (res.result === '1') {
            this.dialogFormVisible2 = false
            this.checkRemark = ''
            this._gameNnUserRoomList()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    }
  },
  created () {
    this._gameNnUserRoomList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



