<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item search-id-input">
        <span class="title">ID </span>
        <el-input v-model="requestData.userId" clearable></el-input>
      </div>
      <div class="search-item search-phone-input">
        <span class="title"></span>
        <el-input v-model="requestData.phone" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <el-tabs type="border-card" style="height: calc(100% - 40px);" @tab-click="handleClick" v-model="type">
      <el-tab-pane label="" name="1">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userId" label="ID"></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="userName" label=""></el-table-column>
            <el-table-column prop="rechargeType" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.rechargeType===1"></span>
                <span v-if="scope.row.rechargeType===2"></span>
              </template>
            </el-table-column>
            <el-table-column prop="rechargePrice" label=""></el-table-column>
            <el-table-column prop="status" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1"></span>
                <span v-if="scope.row.status===2"></span>
                <span v-if="scope.row.status===3"></span>
              </template>
            </el-table-column>
            <el-table-column prop="createDate" label=""></el-table-column>
            <el-table-column prop="registerDate" fixed="right" label="" width="300">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="open(1, scope.row.id)"></el-button>
                <el-button size="mini" type="primary" @click="open(2, scope.row.id)"></el-button>
                <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="2">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userId" label="ID"></el-table-column>
            <el-table-column prop="userName" label=""></el-table-column>
            <el-table-column prop="rechargeType" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.rechargeType===1"></span>
                <span v-if="scope.row.rechargeType===2"></span>
              </template>
            </el-table-column>
            <el-table-column prop="rechargePrice" label=""></el-table-column>
            <el-table-column prop="status" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1"></span>
                <span v-if="scope.row.status===2"></span>
                <span v-if="scope.row.status===3"></span>
              </template>
            </el-table-column>
            <el-table-column prop="checkSysUserName" label=""></el-table-column>
            <el-table-column prop="checkStatus" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.checkStatus===1"></span>
                <span v-if="scope.row.checkStatus===2"></span>
                <span v-if="scope.row.checkStatus===3"></span>
              </template>
            </el-table-column>
            <el-table-column prop="checkRemark" label=""></el-table-column>
            <el-table-column prop="checkDate" label="" width="150"></el-table-column>
            <el-table-column prop="createDate" label="" width="150"></el-table-column>
            <el-table-column fixed="right" label="" width="100">
              <template slot-scope="scope">
                <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>ID</label>{{dialogData.userId}}</p>
        <p><label></label>{{dialogData.userName}}</p>
        <p><label></label><span v-if="dialogData.rechargeType===1"></span><span v-if="dialogData.rechargeType===2"></span></p>
        <p><label></label>{{dialogData.rechargePrice}}</p>
        <p><label></label>{{dialogData.givePrice}}</p>
        <p><label></label><span v-if="dialogData.status===1"></span><span v-if="dialogData.status===2"></span><span v-if="dialogData.status===3"></span></p>
        <p><label></label>{{dialogData.checkSysUserName}}</p>
        <p><label></label><span v-if="dialogData.checkStatus===1"></span><span v-if="dialogData.checkStatus===2"></span><span v-if="dialogData.checkStatus===3"></span></p>
        <p><label></label>{{dialogData.checkRemark}}</p>
        <p><label></label>{{dialogData.checkDate}}</p>
        <p><label></label>{{dialogData.createDate}}</p>
        <p v-if="dialogData.rechargeType===2"><label></label>{{dialogData.bankName}}</p>
        <p v-if="dialogData.rechargeType===2"><label></label>{{dialogData.cardHolder}}</p>
        <p v-if="dialogData.rechargeType===2"><label></label>{{dialogData.bankCardNo}}</p>
        <p v-if="dialogData.rechargeType===2"><label></label>{{dialogData.openingBank}}</p>
        <p v-if="dialogData.rechargeType===1"><label></label>{{dialogData.aliPayName}}</p>
        <p v-if="dialogData.rechargeType===1"><label></label>{{dialogData.aliPayNo}}</p>
        <p><label></label>{{dialogData.paymentName}}</p>
        <p><label></label>{{dialogData.paymentRemark}}</p>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisible2" center width="300px">
      <div class="plan-con">
        <div>
          <el-input type="textarea" placeholder="" size="mini" v-model="checkRemark"></el-input>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false;"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {userRechargeList, userRechargeInfo, userRechargeUpdateStatus} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', type: '1', userId: '', phone: ''},
      options: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 4, label: ''}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      dialogFormVisible2: false,
      checkRemark: '',
      id: '',
      type: '1'
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._userRechargeList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._userRechargeList()
    },
    handleClick (val) {
      this.type = val.name
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', type: val.name}
      this._userRechargeList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userRechargeList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', type: this.type}
      this._userRechargeList()
    },
    async _userRechargeList () {
      const res = await userRechargeList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    open (s, id) {
      this.temp = s
      this.id = id
      this.dialogFormVisible2 = true
    },
    submit () {
      if (this.checkRemark === '') {
        this.$message({type: 'warning', message: ''})
      } else {
        let tip
        this.temp === 1 ? tip = '' : tip = ''
        this.$confirm(tip, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const res = await userRechargeUpdateStatus({rechargeId: this.id, checkStatus: this.temp, checkRemark: this.checkRemark})
          if (res.result === '1') {
            this.dialogFormVisible = false
            this.dialogFormVisible2 = false
            this.checkRemark = ''
            this._userRechargeList()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    async detailsFn (row) {
      const res = await userRechargeInfo({rechargeId: row.id})
      if (res.result === '0') {
        this.dialogData = res.datas
      }
      this.dialogFormVisible = true
    }
  },
  created () {
    this._userRechargeList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>
<style>
.el-tabs--border-card>.el-tabs__content{
  height: calc(100% - 50px);
}
.el-tab-pane{
  height: 100%;
}
</style>



