<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item five">
        <span class="title">ID</span>
        <el-input v-model="serachData.parentId" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="serachData.areaName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="serachData.areaLevel" placeholder="">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="countryAreaId" label="ID"></el-table-column>
        <el-table-column prop="countryName" label=""></el-table-column>
        <el-table-column prop="stateName" label="/"></el-table-column>
        <el-table-column prop="cityName" label=""></el-table-column>
        <el-table-column prop="regionName" label=""></el-table-column>
        <el-table-column prop="parentId" label="ID"></el-table-column>
        <el-table-column prop="areaLevel" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.areaLevel === 1"></span>
            <span v-if="scope.row.areaLevel === 2">/</span>
            <span v-if="scope.row.areaLevel === 3"></span>
            <span v-if="scope.row.areaLevel === 4"></span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import {areaList} from '@/api'
export default {
  data () {
    return {
      totalNum: 0,
      serachData: {parentId: '', areaName: '', areaLevel: '', pageNum: 1, pageSize: 20},
      options: [{value: 1, label: ''}, {value: 2, label: '/'}, {value: 3, label: ''}, {value: 4, label: ''}],
      tableData: []
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.serachData.pageSize = val
      this._areaList()
    },
    handleCurrentChange (val) { // page
      this.serachData.pageNum = val
      this._areaList()
    },
    async _areaList () {
      const res = await areaList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.countryAreaList
      }
    },
    refresh () {
      this.serachData = {parentId: '', areaName: '', areaLevel: '', pageNum: 1, pageSize: 20}
      this._areaList()
    },
    search () {
      this.serachData.pageNum = 1
      this._areaList()
    }
  },
  created () {
    this._areaList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
</style>



