<!--  -->
<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item three">
        <span class="title"></span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="nnAwardId" label="ID" width="100"></el-table-column>
        <el-table-column prop="roomName" label="" width="100"></el-table-column>
        <el-table-column prop="beginBetDate" label=""></el-table-column>
        <el-table-column prop="endBetDate" label="" width="100"></el-table-column>
        <el-table-column prop="sendDate" label=""></el-table-column>
        <el-table-column prop="awardDate" label="" width="100"></el-table-column>
        <el-table-column prop="resultDate" label="" width="100"></el-table-column>
        <el-table-column prop="awardStatus" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.awardStatus===1" style="color: #666"></span>
            <span v-if="scope.row.awardStatus===5" style="color: #eb453c"></span>
            <span v-if="scope.row.awardStatus===2" style="color: green"></span>
            <span v-if="scope.row.awardStatus===3" style="color: #eb453c"></span>
            <span v-if="scope.row.awardStatus===4" style="color: #eb453c"></span>
            <span v-if="scope.row.awardStatus===6" style="color: #666"></span>
          </template>
        </el-table-column>
        <el-table-column prop="totalBetPrice" label="" width="100"></el-table-column>
        <el-table-column prop="sendPrice" label="" width="100"></el-table-column>
        <el-table-column prop="openPrice" label=""></el-table-column>
        <el-table-column prop="betPeopleNum" label=""></el-table-column>
        <el-table-column prop="betRobotNum" label=""></el-table-column>
        <el-table-column prop="betWaterRatio" label=""></el-table-column>
        <el-table-column prop="betWaterPrice" label=""></el-table-column>
        <el-table-column prop="bonusPoolNum" label=""></el-table-column>
        <el-table-column prop="createDate" label=""></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>ID</label>{{dialogData.nnAwardId}}</p>
        <p><label></label>{{dialogData.roomName}}</p>
        <p><label></label>{{dialogData.beginBetDate}}</p>
        <p><label></label>{{dialogData.endBetDate}}</p>
        <p><label></label>{{dialogData.sendDate}}</p>
        <p><label></label>{{dialogData.awardDate}}</p>
        <p><label></label>{{dialogData.resultDate}}</p>
        <p><label></label>{{dialogData.awardStatus}}</p>
        <p><label></label>{{dialogData.totalBetPrice}}</p>
        <p><label></label>{{dialogData.sendPrice}}</p>
        <p><label></label>{{dialogData.openPrice}}</p>
        <p><label></label>{{dialogData.betPeopleNum}}</p>
        <p><label></label>{{dialogData.betRobotNum}}</p>
        <p><label></label>{{dialogData.betWaterRatio}}</p>
        <p><label></label>{{dialogData.betWaterPrice}}</p>
        <p><label></label>{{dialogData.bonusPoolNum}}</p>
        <p><label></label>{{dialogData.createDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameNnAwardNnAwardList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, roomName: ''},
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._gameNnAwardNnAwardList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._gameNnAwardNnAwardList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameNnAwardNnAwardList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._gameNnAwardNnAwardList()
    },
    async _gameNnAwardNnAwardList () {
      const res = await gameNnAwardNnAwardList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameNnAwardNnAwardList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



