<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="serachData.appName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="serachData.updateType" placeholder="">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="serachData.updateContent" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addVersion"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(1)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(2)"></el-button>
        <!-- <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)"></el-button> -->
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="versionList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="versionId" label="ID" width="60"></el-table-column>
        <el-table-column prop="appName" label="" width="100"></el-table-column>
        <el-table-column prop="updateType" label="" width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.updateType === 1" style="color:#eb453c"></span>
            <span v-if="scope.row.updateType === 2" style="color:#67c23a"></span>
            <span v-if="scope.row.updateType === 3" style="color:#999"></span>
          </template>
        </el-table-column>
        <el-table-column prop="versionName" label="" width="120"></el-table-column>
        <el-table-column prop="apkCode" label="APK" width="100"></el-table-column>
        <el-table-column prop="apkPackage" label="APK" width="120"></el-table-column>
        <el-table-column prop="apkSize" label="APK" width="100"></el-table-column>
        <el-table-column prop="updateContent" label="" mim-width="150"></el-table-column>
        <el-table-column prop="updateDate" label="" width="140"></el-table-column>
        <el-table-column prop="versionUrl" label="" width="150"></el-table-column>
        <el-table-column prop="createSysUserName" label="" width="100"></el-table-column>
        <el-table-column prop="status" label="" width="60">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a"></span>
            <span v-if="scope.row.status === 2" style="color:#eb453c"></span>
            <span v-if="scope.row.status === 3" style="color:#999"></span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="isedit ? '': ''" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="90px" size="mini">
        <el-form-item label="">
          <el-input style="width:192px" v-model="form.appName" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select clearable v-model="form.updateType" placeholder="">
            <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" :rows="3" style="width:192px" v-model="form.updateContent" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
          size="mini"
          style="width: 187px;"
          v-model="form.updateDate"
          type="datetime"
          value-format="yyyy-MM-dd HH:mm:ss"
          placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <input type="file" ref="uploadInput" @change="_uploadChange" accept=".apk">
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {uploadApk, versionList, versionAdd, versionUpdate, versionUpdateStatus} from '@/api'
export default {
  data () {
    return {
      dialogFormVisible: false,
      isedit: false,
      form: {appName: '', updateType: '', updateContent: '', updateDate: '', versionUrl: ''},
      choseArr: [],
      serachData: {appName: '', updateType: '', updateContent: ''},
      versionList: [],
      roleList: [],
      userid: '',
      options: [{value: 1, label: ''}, {value: 2, label: ''}]
    }
  },
  methods: {
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _uploadChange (e) {
      this.key = new FormData()
      this.key.append('file', e.target.files[0])
      const res = await uploadApk(this.key)
      if (res.result === '1') {
        this.form.versionUrl = res.datas.fileUrl
      }
    },
    edit () {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: ''})
      } else {
        this.dialogFormVisible = true
        this.form.versionId = this.choseArr[0].versionId
        this.form.appName = this.choseArr[0].appName
        this.form.updateType = this.choseArr[0].updateType
        this.form.updateContent = this.choseArr[0].updateContent
        this.form.updateDate = this.choseArr[0].updateDate
        this.form.versionUrl = this.choseArr[0].versionUrl
        this.isedit = true
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: ''})
      } else {
        let tip
        row === 1 ? tip = '' : row === 2 ? tip = '' : tip = ''
        this.$confirm(tip, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    async _freeze (row) {
      const res = await versionUpdateStatus({status: row, versionId: this.choseArr[0].versionId})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.getVersionList()
      }
    },
    async getVersionList () {
      const res = await versionList(this.serachData)
      if (res.result === '0') {
        this.versionList = res.datas.versionList
      }
    },
    async submit () {
      if (this.form.appName === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.updateType === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.updateContent === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.updateDate === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.versionUrl === '') {
        this.$message({type: 'warning', message: ''})
      } else if (!this.isedit) {
        const res = await versionAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this.serachData.pageNum = 1
          this.getVersionList()
          this.setReset()
        }
      } else {
        const res = await versionUpdate(this.form)
        if (res.result === '1') {
          this.getVersionList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addVersion () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {appName: '', updateType: '', updateContent: '', updateDate: '', versionUrl: ''}
    },
    search () {
      this.serachData.pageNum = 1
      this.getVersionList()
    },
    refresh () {
      this.serachData = {appName: '', updateType: '', updateContent: ''}
      this.getVersionList()
    }
  },
  created () {
    this.getVersionList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 40px);
  }
  .el-dialog__body .my-table{
    height: 350px;
  }
}
</style>



