<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="requestData.luckStatus" placeholder="">
          <el-option v-for="(item, index) in options2" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="requestData.receiveType" placeholder="">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div><div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="requestData.lossStatus" placeholder="">
          <el-option v-for="(item, index) in options1" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label=""></el-table-column>
        <el-table-column prop="roomId" label="ID" width="80"></el-table-column>
        <el-table-column prop="bankName" label="" min-width="180">
          <template slot-scope="scope">
            <div style="float:left;">
              <img style="display: inline-block;width: 30px;vertical-align: middle;" :src="baseUrl + scope.row.roomLogo" alt="">
              {{scope.row.roomName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="sendUserName" label=""></el-table-column>
        <el-table-column prop="receiveType" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.receiveType===1"></span>
            <span v-if="scope.row.receiveType===2"></span>
            <span v-if="scope.row.receiveType===3"></span>
          </template>
        </el-table-column>
        <el-table-column prop="receivePrice" label=""></el-table-column>
        <el-table-column prop="lossStatus" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.lossStatus===1"></span>
            <span v-if="scope.row.lossStatus===2"></span>
          </template>
        </el-table-column>
        <el-table-column prop="luckStatus" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.luckStatus===1"></span>
            <span v-if="scope.row.luckStatus===2"></span>
          </template>
        </el-table-column>
        <el-table-column prop="receiveDate" label=""></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label></label>{{dialogData.userName}}</p>
        <p><label></label>{{dialogData.roomName}}</p>
        <p><label>logo</label><img :src="baseUrl + dialogData.roomLogo" @click="showImg(baseUrl + dialogData.roomLogo)" alt=""></p>
        <p><label></label>{{dialogData.sendUserName}}</p>
        <p><label></label><span v-if="dialogData.receiveType===2"></span><span v-if="dialogData.receiveType===3"></span></p>
        <p><label></label>{{dialogData.receivePrice}}</p>
        <p><label></label><span v-if="dialogData.lossStatus===1"></span><span v-if="dialogData.lossStatus===2"></span></p>
        <p><label></label><span v-if="dialogData.luckStatus===1"></span><span v-if="dialogData.luckStatus===2"></span></p>
        <p><label></label>{{dialogData.receiveDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlReceiveInfoList} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      requestData: {pageNum: 1, pageSize: 20, userName: '', luckStatus: '', receiveType: '', lossStatus: '', roomName: ''},
      options: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}],
      options1: [{value: 1, label: ''}, {value: 2, label: ''}],
      options2: [{value: 1, label: ''}, {value: 2, label: ''}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._gameSlReceiveInfoList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._gameSlReceiveInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlReceiveInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', luckStatus: '', receiveType: '', lossStatus: '', roomName: ''}
      this._gameSlReceiveInfoList()
    },
    async _gameSlReceiveInfoList () {
      const res = await gameSlReceiveInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.receiveInfoList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._gameSlReceiveInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



