<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addRole"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)"></el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="Id" width="60"></el-table-column>
        <el-table-column prop="noticeTitle" label="" width="150"></el-table-column>
        <el-table-column prop="noticeContent" label=""></el-table-column>
        <el-table-column prop="status" label="" width="60">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a"></span>
            <span v-if="scope.row.status === 2" style="color:#eb453c"></span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="" width="100"></el-table-column>
        <el-table-column prop="createDate" label="" width="140"></el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="edit(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '': ''" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="100px" size="mini">
        <el-form-item label="">
          <el-input style="width:180px" v-model="form.noticeTitle" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" :rows="5" style="width:180px" v-model="form.noticeContent" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {noticeList, noticeAdd, noticeInfo, noticeEdit, noticeUpdateStatus} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      dialogFormVisible: false,
      isedit: false,
      form: {noticeTitle: '', noticeContent: ''},
      choseArr: [],
      totalNum: 0,
      serachData: {pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      checkedId: '',
      checked: [],
      advertIds: []
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.serachData.pageSize = val
      this._noticeList()
    },
    handleCurrentChange (val) { // page
      this.serachData.pageNum = val
      this._noticeList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _noticeList () {
      const res = await noticeList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.noticeList
      }
    },
    async submit () {
      if (this.form.noticeTitle === '') {
        this.$message({type: 'warning', message: ''})
      } else if (this.form.noticeContent === '') {
        this.$message({type: 'warning', message: ''})
      } else if (!this.isedit) {
        this.form.noticeType = 1
        const res = await noticeAdd(this.form)
        if (res.result === '1') {
          this._noticeList()
          this.dialogFormVisible = false
          this.setReset()
        }
      } else {
        const res = await noticeEdit(this.form)
        if (res.result === '1') {
          this._noticeList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addRole () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    async edit (row) {
      this.dialogFormVisible = true
      const res = await noticeInfo({id: row.id})
      this.form.id = row.id
      this.form.noticeTitle = res.datas.noticeTitle
      this.form.noticeContent = res.datas.noticeContent
      this.isedit = true
    },
    async _freeze (row) {
      const res = await noticeUpdateStatus({status: row, id: this.choseArr[0].id})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.advertIds = []
        this._noticeList()
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '!'})
      } else {
        let tip
        row === 1 ? tip = '' : row === 2 ? tip = '' : tip = ''
        this.$confirm(tip, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {noticeTitle: '', noticeContent: ''}
    },
    refresh () {
      this.serachData = {pageNum: 1, pageSize: 20}
      this._noticeList()
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._noticeList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
}
.editor-container {
  max-height: 500px;
  margin: 0 0 30px;
  .editor-upload-btn-container {
    text-align: right;
    margin-right: 10px;
    .editor-upload-btn {
      display: inline-block;
    }
  }
}
</style>



