<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title"></span>
        <el-select clearable v-model="requestData.infoType" placeholder="">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label=""></el-table-column>
        <el-table-column prop="infoType" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.infoType===1"></span>
            <span v-if="scope.row.infoType===2"></span>
            <span v-if="scope.row.infoType===3"></span>
            <span v-if="scope.row.infoType===4"></span>
            <span v-if="scope.row.infoType===5"></span>
            <span v-if="scope.row.infoType===6"></span>
            <span v-if="scope.row.infoType===11"></span>
            <span v-if="scope.row.infoType===12"></span>
            <span v-if="scope.row.infoType===13"></span>
            <span v-if="scope.row.infoType===14"></span>
            <span v-if="scope.row.infoType===15"></span>
            <span v-if="scope.row.infoType===16"></span>
            <span v-if="scope.row.infoType===17"></span>
            <span v-if="scope.row.infoType===18"></span>
            <span v-if="scope.row.infoType===19"></span>
            <span v-if="scope.row.infoType===21"></span>
            <span v-if="scope.row.infoType===22"></span>
            <span v-if="scope.row.infoType===23"></span>
            <span v-if="scope.row.infoType===24"></span>
            <span v-if="scope.row.infoType===25"></span>
            <span v-if="scope.row.infoType===26"></span>
            <span v-if="scope.row.infoType===27"></span>
            <span v-if="scope.row.infoType===28"></span>
            <span v-if="scope.row.infoType===29"></span>
            <span v-if="scope.row.infoType===30"></span>
            <span v-if="scope.row.infoType===31"></span>
            <span v-if="scope.row.infoType===32"></span>
            <span v-if="scope.row.infoType===33"></span>
          </template>
        </el-table-column>
        <el-table-column prop="validNum" label=""></el-table-column>
        <el-table-column prop="priceNum" label=""></el-table-column>
        <el-table-column prop="createDate" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green"></span>
            <span v-if="scope.row.status===2" style="color: #eb453c"></span>
          </template>
        </el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label></label>{{dialogData.userName}}</p>
        <p><label></label>
          <span v-if="dialogData.infoType===1"></span>
          <span v-if="dialogData.infoType===2"></span>
          <span v-if="dialogData.infoType===3"></span>
          <span v-if="dialogData.infoType===4"></span>
          <span v-if="dialogData.infoType===5"></span>
          <span v-if="dialogData.infoType===6"></span>
          <span v-if="dialogData.infoType===11"></span>
          <span v-if="dialogData.infoType===12"></span>
          <span v-if="dialogData.infoType===13"></span>
          <span v-if="dialogData.infoType===14"></span>
          <span v-if="dialogData.infoType===15"></span>
          <span v-if="dialogData.infoType===16"></span>
          <span v-if="dialogData.infoType===17"></span>
          <span v-if="dialogData.infoType===18"></span>
          <span v-if="dialogData.infoType===19"></span>
          <span v-if="dialogData.infoType===21"></span>
          <span v-if="dialogData.infoType===22"></span>
          <span v-if="dialogData.infoType===23"></span>
          <span v-if="dialogData.infoType===24"></span>
          <span v-if="dialogData.infoType===25"></span>
          <span v-if="dialogData.infoType===26"></span>
          <span v-if="dialogData.infoType===27"></span>
          <span v-if="dialogData.infoType===28"></span>
          <span v-if="dialogData.infoType===29"></span>
          <span v-if="dialogData.infoType===30"></span>
          <span v-if="dialogData.infoType===31"></span>
          <span v-if="dialogData.infoType===32"></span>
          <span v-if="dialogData.infoType===33"></span>
        </p>
        <p><label></label>{{dialogData.validNum}}</p>
        <p><label></label>{{dialogData.priceNum}}</p>
        <p><label></label>{{dialogData.createDate}}</p>
        <p><label></label><span v-if="dialogData.status===1"  style="color: green"></span><span v-if="dialogData.status===2" style="color: #eb453c"></span></p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {userUserAccountInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', infoType: ''},
      options: [
        {value: 1, label: ''},
        {value: 2, label: ''},
        {value: 3, label: ''},
        {value: 4, label: ''},
        {value: 5, label: ''},
        {value: 6, label: ''},
        {value: 11, label: ''},
        {value: 12, label: ''},
        {value: 13, label: ''},
        {value: 14, label: ''},
        {value: 15, label: ''},
        {value: 16, label: ''},
        {value: 17, label: ''},
        {value: 18, label: ''},
        {value: 19, label: ''},
        {value: 21, label: ''},
        {value: 22, label: ''},
        {value: 23, label: ''},
        {value: 24, label: ''},
        {value: 25, label: ''},
        {value: 26, label: ''},
        {value: 27, label: ''},
        {value: 28, label: ''},
        {value: 29, label: ''},
        {value: 30, label: ''},
        {value: 31, label: ''},
        {value: 32, label: ''},
        {value: 33, label: ''}
      ],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._userUserAccountInfoList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._userUserAccountInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userUserAccountInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', infoType: ''}
      this._userUserAccountInfoList()
    },
    async _userUserAccountInfoList () {
      const res = await userUserAccountInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._userUserAccountInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



