<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item three">
        <span class="title"></span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh"></el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label="" width="100"></el-table-column>
        <el-table-column prop="sendUserName" label=""></el-table-column>
        <el-table-column prop="middleUserName" label="" width="100"></el-table-column>
        <el-table-column prop="receivePrice" label=""></el-table-column>
        <el-table-column prop="userLevel" label="" width="100"></el-table-column>
        <el-table-column prop="waterRadio" label="" width="100"></el-table-column>
        <el-table-column prop="waterPrice" label="" width="100"></el-table-column>
        <el-table-column prop="validNum" label="" width="100"></el-table-column>
        <el-table-column prop="waterDate" label=""></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label></label>{{dialogData.userName}}</p>
        <p><label></label>{{dialogData.sendUserName}}</p>
        <p><label></label>{{dialogData.middleUserName}}</p>
        <p><label></label>{{dialogData.receivePrice}}</p>
        <p><label></label>{{dialogData.userLevel}}</p>
        <p><label></label>{{dialogData.waterRadio}}</p>
        <p><label></label>{{dialogData.waterPrice}}</p>
        <p><label></label>{{dialogData.validNum}}</p>
        <p><label></label>{{dialogData.waterDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlWaterInfoSlWaterInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: ''},
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 
      this.requestData.pageSize = val
      this._gameSlWaterInfoSlWaterInfoList()
    },
    handleCurrentChange (val) { // page
      this.requestData.pageNum = val
      this._gameSlWaterInfoSlWaterInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlWaterInfoSlWaterInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._gameSlWaterInfoSlWaterInfoList()
    },
    async _gameSlWaterInfoSlWaterInfoList () {
      const res = await gameSlWaterInfoSlWaterInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameSlWaterInfoSlWaterInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



